}

.e-rtl {
  direction: rtl;
  text-align: right;
}

.e-overlay {
  background-color: #383838;
  filter: alpha(opacity=50);
  height: 100%;
  opacity: .5;
  pointer-events: none;
  touch-action: none;
  width: 100%;
}

.e-hidden {
  display: none;
}

.e-blazor-hidden {
  visibility: hidden;
}

.e-disabled {
  background-image: none;
